// // GRIDS
.simple-cards-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  grid-gap: 30px;
}

.main-app {
  min-height: 100vh;
  display: grid;
  grid-template-rows: 1fr 10fr;

  .app-header {
    grid-row: 1;
    grid-column: 1;
  }

  .container {
    grid-row: 2;
    grid-column: 1;
  }
}
.container {
  display: grid;
  grid-template-columns: 30fr 1fr;
  .app-nav {
    grid-column: 2;
  }
  .note-index {
    margin-top: 150px;
    grid-column: 1;
    grid-row: 1;
  }
}
