.hamburger-section {
  display: flex;
  gap: 15px;
  margin-right: 10px;

  p {
    color: #5f6368;
    font-family: 'Product Sans', Arial, sans-serif;
    padding-right: 8px;
  }
}

.user-actions {
  display: flex;
  gap: 15px;
  .user-icon {
    margin-left: 15px;
  }
}
