.note-container {
  border-radius: 20px;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  .note-actions-container {
    margin-bottom: 0;
    display: flex;
    gap: 15px;
    opacity: 0;
  }
  &:hover {
    .note-actions-container {
      opacity: 1;
    }
  }
}

.pin-icon {
  position: absolute;
  top: 2px;
  left: 2px;
}
.pinned-notes-section {
  margin-bottom: 150px;
}
