.note-todos-preview {
  padding: 1em;
  .todo-preview {
    display: flex;
    justify-content: space-between;
  }
  .todo {
    display: flex;
    gap: 15px;

    button {
      background-color: transparent;
      border: none;
    }
  }
}
.done {
  text-decoration: line-through;
}
